import UniIcon from "@/components/uni-icons/uni-icons.vue"
import WucTab from '@/components/wuc-tab/wuc-tab.vue'
import JoinUs from '../../components/join-us/join-us.vue'
export default {
	data() {
		return {
			// 默认上来是0,代表账号密码登录
			tabNumber: 0,
			//tab栏标题
			tabNameList: [{
				name: '账号密码登录'
			}, {
				name: '手机验证码登录'
			}],
			username: '',
			password: '',
			phoneUmber: '',
			validcode: ''
		}
	},
	components: {
		WucTab,
		JoinUs,
		UniIcon
	},
	methods: {
		//监听swiper改变修改tab
		swiperChange(e) {
			this.tabNumber = e.detail.current
		},
		//用户名密码登录
		doUsernamePasswordLogin() {
			if (this.phoneUmber === '' || this.password === '') {
				uni.showModal({
					title: '',
					content: '请将信息填写完整后登录',
					showCancel: false,
					success: function(res) {
						if (res.confirm) {
							console.log('用户点击确定');
						}
					}
				})
			} else {


			}
		}
	}
}
